@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
body {
  /* background: url(https://cdn.discordapp.com/attachments/796456212509753384/796471249680859146/unknown.png) no-repeat; */
  /* background-size: 100%; */

  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  --main-color:rgba(73, 78, 80, 0.74);
  --main-color-dark: rgba(77, 80, 82, 0.78);
  --main-color-light: rgba(196, 207, 211, 0.78);
  --secondary-color: rgb(244, 244, 244);
  font-family: 'PT Sans Narrow', sans-serif;
  color: white;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-button {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-thumb {
  background: var(--main-color-light);
  border: 0px none #ffffff;
  border-radius: 50px;
}

::-webkit-scrollbar-thumb:hover {
  background: #ffffff;
}


::-webkit-scrollbar-track {
  background: transparent;
  border: 0px none #ffffff;
  border-radius: 50px;
}

::-webkit-scrollbar-track:hover {
  background: #66666600;
}

::-webkit-scrollbar-track:active {
  background: #33333300;
}

::-webkit-scrollbar-corner {
  background: transparent;
}

.overlay {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background:rgba(73, 78, 80, 0.719);
}

.inventory-money-container {
  width: 10%;
  height: 30%;
  margin-top: 30.5%;
  margin-left: 45%;
  position: absolute;
}

.special-slot-container {
  position: absolute;
  width: 29%; 
  margin-left: 25%;
  margin-top: 35%;
}

.inventory-money {
  background: var(--main-color-dark);;
  margin-bottom: 10%;
  padding: 6%;
  position: relative;
  border-radius: 2px;
  border-top: 2px solid var(--secondary-color);
}

.overlay-inner {
  transform: scale(0.85);
  position: absolute;
  height: 100%;
  width: 100%;
  margin-top: -2%;
}

.inventory-info-box {
  position: absolute;
  background: var(--main-color-dark);
  padding: 0.8%;
  margin-top: 19%;
  margin-left: calc(45% - 3.35%);
  border: 1px solid rgba(141, 143, 144, 0.52);
  z-index: 20;
  opacity: 0;
  width: 15%;
  min-height: 5%;
  border-radius: 25px;
  overflow-wrap: break-word;
}

.inventory-info-box-item-label {
  font-size: 150%;
  font-weight: bold;
  text-align: center;
  padding: 2px;
}

.inventory-info-box-item-text {
  text-align: center;
  padding: 5%;
  font-size: 105%;
  -webkit-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
}

.inventory-info-box-item-image {  
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  left: 0%;
  top: 0%;
}

.inventory-box-logo {
  position: absolute;
  background-image: url(https://cdn.discordapp.com/attachments/508316536696602627/541695214373306378/underground-logo-transparent_1.png);
  background-repeat: no-repeat;
  background-size: 100%;
  left: 44.37%;
  width: 11%;
  top: 6%;
  height: 20%;
  opacity: 0.70;
}

#inventory-money-bank:hover #inventory-money-code {
  opacity: 1;
}

.inventory-money:hover {
  opacity: 0.9;
}

.inventory-money-code {
  background: var(--main-color-light);
  width: 24%;
}

#inventory-money-code:hover {
  opacity: 0.9;
}

#inventory-money-code {
  opacity: 1;
}

.inventory-money-label {
  text-align: center;
  font-weight: bold;
  font-size: 120%;
}

.inventory-money-money {
  text-align: center;
}

.inventory-box-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
}

.inventory-box-label {
  position: absolute;
  margin-top: -4.6%;
}

.inventory-box {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  
  width: 29%;
  height: 60%;
  margin: auto;
  overflow: auto;
  padding: 0.3%;
}

.inventory-box-tabs {
  width: 29.2%;
  margin-top: -2.8%;
  height: 4%;
  position: absolute;
  z-index: 99999;
}

.inventory-box-tab {
  float: left;
  margin-top: 2%;
  margin-right: 3%;
  border-radius: 10px;
  min-width: 12%;
  text-align: center;
  margin-left: 0.38%;
  padding: 1%;
  background: var(--main-color-dark);
  z-index: 99999;
}

.inventory-box-tab-selected {
  border: 1px solid var(--secondary-color);
}

.inventory-slot {
  background:  rgba(77, 80, 82, 0.705);
  float: left;
  width: 23%;
  margin: 0.5%;
  height: 14vh;
  border-radius: 12px;
  /* border: 1px solid rgba(244, 244, 244, 0.562); */
  position: relative;
}

.inventory-slot-active {
  background:rgba(73, 78, 80, 0.904);
  float: left;
  width: 23%;
  margin: 0.5%;
  height: 14vh;
  border-radius: 12px;
  /* border: 1px solid rgba(244, 244, 244, 0.562); */
  position: relative;
}

.inventory-slot-label {
  position: absolute;
  background: rgba(141, 143, 144, 0.52);
  width: 100%;
  bottom: 0;
  height: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  overflow: hidden;
}

.inventory-slot-number {
  position: absolute;
  color: var(--main-color-light);
  opacity: 80%;
  margin-top: -5%;
  margin-left: 2.5%;
  font-size: 225%;
}

.inventory-box-amount {
  position: absolute;
  margin-top: -4.6%;
  margin-left: 24%;
  font-size: 120%;
}

.item {
  position: relative;
  height: 83%;
}

.item-count {
  margin-left: 5%;
  margin-top: 2%;
  position: absolute;
}

.item-bar {
  position: absolute;
  background-color: transparent;
  /* border: 1px red solid; */
  text-align: center;
  margin-top: 89%;
  max-width: 100%;
}

.item-image {
  position: absolute;
  height: 100%;
  width: 100%;
}

.item-weight {
  float: right;
  margin-right: 6%;
  margin-top: 2%;
}

.ui-draggable-dragging {
  height: 11.6%;
  width: 6.7%;
}

.ui-droppable-hover {
  background: #616674 !important;
}

.inventory-box-buttons {
  position: absolute;
  height: 30%;
  width: 10%;
  margin-left: 45%;
  margin-top: 15%;
  overflow: hidden;
}
.hidden {
  background:repeating-linear-gradient(45deg, rgba(167, 165, 165, 0.116) 0%, rgba(62, 62, 62, 0.082) 3%);
  height: 100%;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.button-input {
  border: none;
  background: var(--main-color-dark);;
  border-radius: 2px;
  color: white;
  padding: 8% 5% 8% 5%;
  width: 89%;
  text-align: center;
  border-bottom: 2px solid var(--secondary-color);
  border-top: 2px solid var(--secondary-color);
}

.button-input:hover {
  opacity: 0.6;
}

.button-input:focus {
  outline: none;
}

.inventory-info-box-animation-enter {
  opacity: 0;
}
.inventory-info-box-animation-enter-active {
  opacity: 1;
  transition: opacity 500ms;
}

.inventory-info-box-animation-enter-done {
  opacity: 1;
}

.inventory-info-box-animation-exit {
  opacity: 1;
}
.inventory-info-box-animation-exit-active {
  opacity: 0;
  transition: opacity 500ms;
}

.lds-dual-ring {
  width: 64px;
  height: 64px;
  transform: scale(0.4);
  float: right;
  margin-top: -10%;
  margin-right: -10%;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid rgba(107, 107, 107, 0.603);
  border-color: rgba(107, 107, 107, 0.603) transparent rgba(107, 107, 107, 0.603) transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.inventory-notification-container {
  position: absolute;
  width: 16%;
  height: 110%;
  right: 0;
  margin-right: -8%;
}

.inventory-notification-container .notification {
  padding: 7%;
  background: var(--main-color-dark);
  font-size: 90%;
  margin: 2%;
  margin-bottom: 4%;
  border-right: 3px solid var(--secondary-color);
}

.inventory-notification-container .notification .notification-header {
  text-shadow: none;
  font-size: 110%;
  margin-bottom: 2%;
}

.inventory-notification-container .notification .notification-content {
  color: #ffffff83;
}

.inventory-notification-enter {
  opacity: 0;
  transform: translate(200%, 0); 
}

.inventory-notification-enter-active {
  opacity: 1;
  transform: translate(0%, 0); 
  transition: 500ms;
}

.inventory-notification-enter-done {
  opacity: 1;
}

.inventory-notification-exit {
  transform: scale(1);
  opacity: 1;
}
.inventory-notification-exit-active {
  transform: scale(0);
  opacity: 0;
  transition: 500ms ease-in-out;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

